        // Á≠âappÊï∞ÊçÆÂä†ËΩΩ‰πãÂêéÂÜçÊâßË°åÈÄªËæë Âê¶Âàô‰ºöÈó™Â±è
        is_show() {
            return this.$store.state.app.ready && this.active_session_qa_data.length === 0;
        }
    },
    watch: {
        // ÁõëÂê¨chatÁöÑÂèòÂåñÔºåÂΩìÂèòÂåñÊó∂ÔºåË°®Á§∫Âú®BotÂõûÁ≠îÔºåÊ≠§Êó∂ÈúÄË¶ÅÂà∑Êñ∞ÊªöÂä®Êù°ÁöÑ‰ΩçÁΩÆ
        "$store.state.app.chat": {
            deep: true, //Ê∑±Â∫¶ÁõëÂê¨ËÆæÁΩÆ‰∏∫ true
            handler: function (newVal, oldVal) {
                // Ê£ÄÊü•ÊòØÂê¶ÊªöÂä®Âà∞Â∫ïÈÉ® Áªô200pxÁöÑËØØÂ∑ÆÊéßÂà∂ ËøôÈáåÁöÑ200pxÂèØ‰ª•Ê†πÊçÆÂÆûÈôÖÈúÄÊ±ÇË∞ÉÊï¥
                let isAtBottom = this.$refs.homeRef.scrollTop + this.$refs.homeRef.clientHeight >= this.$refs.homeRef.scrollHeight - 200;
                if (!isAtBottom) {
                    return;
                }
                // ÂÜÖÂÆπÊõ¥Êñ∞Êó∂Ôºå‰øùÊåÅÊªöÂä®Êù°ÁöÑ‰ΩçÁΩÆ
                this.$nextTick(() => {
                    this.$refs.homeRef.scrollTop = this.$refs.homeRef.scrollHeight
                });
            }
        },
        // ÁõëÂê¨activeÁöÑÂèòÂåñÔºåÂΩìÂèòÂåñÊó∂ÔºåË°®Á§∫ÂàáÊç¢‰∫ÜËÅäÂ§©ÈÄâÈ°πÂç°ÔºåÊ≠§Êó∂ÈúÄË¶ÅÊääÊªöÂä®Êù°ÁöÑ‰ΩçÁΩÆËÆæÁΩÆÂà∞ÊúÄ‰∏ãÊñπ
        "$store.state.app.active": {
            deep: true, //Ê∑±Â∫¶ÁõëÂê¨ËÆæÁΩÆ‰∏∫ true
            handler: function (newVal, oldVal) {
                // ÂÜÖÂÆπÊõ¥Êñ∞Êó∂Ôºå‰øùÊåÅÊªöÂä®Êù°ÁöÑ‰ΩçÁΩÆ
                this.$nextTick(() => {
                    this.$refs.homeRef.scrollTop = this.$refs.homeRef.scrollHeight
                });
            }
        },
        query: function (newVal, oldVal) {
            // ÂÜÖÂÆπÊõ¥Êñ∞Êó∂Ôºå‰øùÊåÅÊªöÂä®Êù°ÁöÑ‰ΩçÁΩÆ
            this.$nextTick(() => {
